/* src/components/HomePage.css */

/* Стилі для логін-форми */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
}

.login-form {
    background-color: #fff;
    padding: 2rem;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 400px;
}

.login-form h2 {
    text-align: center;
    margin-bottom: 1.5rem;
    color: #333;
}

.input-group {
    margin-bottom: 1.5rem;
}

.input-group label {
    display: block;
    margin-bottom: 0.5rem;
    font-weight: bold;
    color: #555;
}

.input-group input {
    width: 100%;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    color: #333;
}

.input-group input:focus {
    outline: none;
    border-color: #007bff;
}

.login-button {
    width: 100%;
    padding: 0.75rem;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
}

.login-button:hover {
    background-color: #0056b3;
}

/* Нові стилі з App.css */

/* Основні стилі */
body {
    margin: 0;
    font-family: Arial, sans-serif;
}

header {
    background-color: #001f3f; /* Темно синій колір */
    color: white;
    border-radius: 5px;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header .logo {
    font-size: 24px;
    font-weight: bold;
}

header nav {
    flex-grow: 1;
    margin-left: 50px;
}

header nav ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    justify-content: center; /* Центр категорій */
    margin: 0;
}

header nav ul li {
    margin: 0 15px;
}

header nav ul li a {
    color: white; /* Колір посилань */
    text-decoration: none;
    padding: 10px 15px;
    display: block;
    transition: background-color 0.3s;
}

header nav ul li a:hover {
    background-color: #0074D9; /* Світліший синій колір при наведенні */
}

/* Стилі для правого меню */
header .right-menu a {
    background-color: #001f3f; /* Початковий колір кнопок */
    color: white;
    padding: 10px 15px;
    border-radius: 5px;
    text-decoration: none;
    margin-left: 10px;
    transition: background-color 0.3s;
}

header .right-menu a:hover {
    background-color: #0056b3; /* Темніший зелений колір при наведенні */
}

/* Основний контент */
.main-content {
    display: flex;
    padding: 20px;
    background-color: #f5f5f5; /* Світло-сірий фон для контрасту */
    height: calc(100vh - 100px); /* Висота контенту за вирахуванням висоти хедера */
    overflow-y: auto; /* Додає вертикальний скролл, якщо контент виходить за межі */
}

.sidebar {
    width: 250px;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-right: 20px;
    height: 100%; /* Займає всю доступну висоту */
    overflow-y: auto; /* Додає вертикальний скролл, якщо контент виходить за межі */
}

.products {
    flex-grow: 1;
    background-color: #fff;
    padding: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    height: 100%; /* Займає всю доступну висоту */
    overflow-y: auto; /* Додає вертикальний скролл, якщо контент виходить за межі */
    display: flex;
    flex-direction: column; /* Елементи всередині будуть розташовані в колонку */
    align-items: flex-start; /* Вирівнювання елементів на початку контейнера */
}

.product-list {
    display: flex;
    flex-wrap: wrap; /* Додає можливість переносу рядків товарів */
    gap: 10px; /* Додає відстань між товарами */
    width: 100%; /* Займає всю доступну ширину */
}

.product-item {
    flex: 1 1 calc(33.333% - 20px); /* Товари займають третину ширини з урахуванням відступів */
    background-color: #eee;
    padding: 10px;
    box-sizing: border-box;
    text-align: center;
}
