/* src/components/Header.css */

.site-header {
    background-color: #001f3f; /* Темно синій колір */
    color: white;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo a {
    color: white; /* Білий колір тексту */
    font-size: 24px;
    font-weight: bold;
    text-decoration: none; /* Відсутність підкреслення */
    transition: color 0.3s;
}

.logo a:hover {
    color: #d6d6d6; /* Світліший колір при наведенні */
}

.main-nav ul {
    list-style-type: none;
    padding: 0;
    display: flex;
    justify-content: center; /* Центр категорій */
    margin: 0;
}

.main-nav ul li {
    margin: 0 15px;
}

.main-nav ul li a {
    color: white; /* Білий колір тексту */
    font-size: 16px; /* Розмір тексту */
    font-weight: bold; /* Жирний шрифт */
    text-decoration: none; /* Відсутність підкреслення */
    transition: color 0.3s;
}

.main-nav ul li a:hover {
    color: #d6d6d6; /* Світліший колір при наведенні */
}

.right-menu a {
    color: white; /* Білий колір тексту */
    font-size: 16px; /* Розмір тексту */
    font-weight: bold; /* Жирний шрифт */
    text-decoration: none; /* Відсутність підкреслення */
    padding: 10px 15px;
    margin-left: 10px;
    transition: color 0.3s;
    border-radius: 5px; /* Закруглені кути для кнопок */
}

.right-menu a:hover {
    color: #d6d6d6; /* Світліший колір при наведенні */
}
